import { StableSwap } from '@saberhq/stableswap-sdk';
import { PublicKey, TransactionInstruction } from '@solana/web3.js';
import { Fraction, ONE, Stable } from '@jup-ag/math';
import JSBI from 'jsbi';
import Decimal from 'decimal.js';
import {
  AccountInfoMap,
  Amm,
  mapAddressToAccountInfos,
  Quote,
  QuoteParams,
  SwapParams,
} from '../amm';
import { createSaberExchangeInstruction } from '../jupiterInstruction';
import { deserializeAccount } from '../../utils/deserializeAccount';
import { AccountInfo } from '@solana/spl-token';
import { ZERO_FRACTION } from '../../constants';

export class SaberAmm implements Amm {
  label = 'Saber';
  private tokenAccounts: AccountInfo[] | undefined;
  private calculator: Stable | undefined;

  constructor(private stableSwap: StableSwap) { }

  get id() {
    return this.stableSwap.config.swapAccount.toBase58();
  }

  getAccountsForUpdate(): PublicKey[] {
    return [
      this.stableSwap.state.tokenA.reserve,
      this.stableSwap.state.tokenB.reserve,
    ];
  }

  update(accountInfoMap: AccountInfoMap) {
    let tokenAccountInfos = mapAddressToAccountInfos(
      accountInfoMap,
      this.getAccountsForUpdate()
    );

    this.tokenAccounts = tokenAccountInfos
      .map((info) => {
        const tokenAccount = deserializeAccount(info.data)
        if (!tokenAccount) {
          throw new Error('Invalid token account data');
        }
        return tokenAccount
      });

    this.calculator = new Stable(
      JSBI.BigInt(this.stableSwap.state.targetAmpFactor.toString()),
      this.tokenAccounts.map((tokenAccount) => {
        return JSBI.BigInt(tokenAccount.amount.toString());
      }),
      [ONE, ONE], // TODO: How to get the token decimal?
      new Fraction(
        this.stableSwap.state.fees.trade.numerator,
        this.stableSwap.state.fees.trade.denominator
      )
    );
  }

  getQuote({ sourceMint, destinationMint, amount }: QuoteParams): Quote {
    if (!this.tokenAccounts) {
      throw new Error('Unable to fetch accounts for specified tokens.');
    }

    if (this.calculator === undefined) {
      throw new Error('Unable to fetch accounts for Curve calculator.');
    }

    const feePct = new Decimal(
      this.stableSwap.state.fees.trade.asFraction.toFixed(4)
    );

    const [inputIndex, outputIndex] = this.tokenAccounts[0].mint.equals(
      sourceMint
    )
      ? [0, 1]
      : [1, 0];

    const result = this.calculator.exchange(
      JSBI.BigInt(amount),
      ZERO_FRACTION,
      inputIndex,
      outputIndex
    );

    return {
      notEnoughLiquidity: false,
      inAmount: amount,
      outAmount: JSBI.toNumber(result.expectedOutputAmount),
      feeAmount: JSBI.toNumber(result.fees),
      feeMint: destinationMint.toBase58(),
      feePct: feePct.toNumber(),
      priceImpactPct: result.priceImpact.toNumber(),
    };
  }

  createSwapInstructions({
    sourceMint,
    sourceTokenAccount,
    destinationTokenAccount,
    userTransferAuthority,
    amount,
    minimumOutAmount,
    platformFee,
  }: SwapParams) {
    // The input or output might require the magic wrapping
    return [createSaberExchangeInstruction(
      this.stableSwap,
      sourceMint,
      sourceTokenAccount,
      destinationTokenAccount,
      userTransferAuthority,
      amount,
      minimumOutAmount,
      platformFee
    )];
  }

  get reserveTokenMints() {
    return [
      this.stableSwap.state.tokenA.mint,
      this.stableSwap.state.tokenB.mint,
    ];
  }
}
