import JSBI from 'jsbi';
import { Curve } from '../math/curve';
import { SwapResult, toDecimal, ZERO } from '../utils';
import Decimal from 'decimal.js';
import { Fraction } from '..';
import exp from 'constants';

export class Stable {
  private curve: Curve;

  constructor(
    amp: JSBI,
    private tokenAmounts: JSBI[],
    private targetPrices: JSBI[],
    private traderFee: Fraction
  ) {
    this.curve = new Curve(
      JSBI.BigInt(this.tokenAmounts.length),
      amp,
      this.tokenAmounts,
      this.targetPrices
    );
  }

  public exchange(
    inputTradeAmount: JSBI,
    slippageTolerance: Fraction,
    inputIndex: number,
    outputIndex: number
  ): SwapResult {
    let outputAmountWithoutFees = this.getOutputAmount(
      inputTradeAmount,
      inputIndex,
      outputIndex
    );
    let fees = this.getFees(outputAmountWithoutFees);
    let expectedOutputAmount = JSBI.subtract(outputAmountWithoutFees, fees);

    return {
      priceImpact: this.getPriceImpact(
        inputTradeAmount,
        expectedOutputAmount,
        inputIndex,
        outputIndex
      ),
      fees: fees,
      expectedOutputAmount: expectedOutputAmount,
      minOutputAmount: this.getMinimumOutputAmount(
        expectedOutputAmount,
        slippageTolerance
      ),
    };
  }

  private getPriceImpact(
    inputTradeAmountJSBI: JSBI,
    expectedOutputAmountJSBI: JSBI,
    inputIndex: number,
    outputIndex: number
  ): Decimal {
    if (
      JSBI.equal(inputTradeAmountJSBI, ZERO) ||
      JSBI.equal(this.tokenAmounts[inputIndex], ZERO) ||
      JSBI.equal(this.tokenAmounts[outputIndex], ZERO)
    ) {
      return new Decimal(0);
    }

    const noSlippageOutputAmount = toDecimal(
      this.getOutputAmountWithNoSlippage(
        inputTradeAmountJSBI,
        inputIndex,
        outputIndex
      )
    );
    const expectedOutputAmount = toDecimal(expectedOutputAmountJSBI);
    const impact = noSlippageOutputAmount
      .sub(expectedOutputAmount)
      .div(noSlippageOutputAmount)
      .abs();

    return impact;
  }

  private getFees(outputAmountWithoutFees: JSBI): JSBI {
    return JSBI.equal(this.traderFee.numerator, ZERO)
      ? ZERO
      : JSBI.divide(
          JSBI.multiply(outputAmountWithoutFees, this.traderFee.numerator),
          this.traderFee.denominator
        );
  }

  private getMinimumOutputAmount(
    expectedOutputAmount: JSBI,
    slippageTolerance: Fraction
  ): JSBI {
    return JSBI.divide(
      JSBI.multiply(
        expectedOutputAmount,
        JSBI.subtract(
          slippageTolerance.denominator,
          slippageTolerance.numerator
        )
      ),
      slippageTolerance.denominator
    );
  }

  private getOutputAmount(
    inputTradeAmount: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    return this.curve.exchange(inputIndex, outputIndex, inputTradeAmount);
  }

  private getOutputAmountWithNoSlippage(
    inputTradeAmount: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    return this.curve.computeBaseY(inputIndex, outputIndex, inputTradeAmount);
  }
}
