import { struct, blob, u8, Structure } from '@solana/buffer-layout';
import { AccountInfo, PublicKey } from '@solana/web3.js';
import { ALDRIN_SWAP_V2_PROGRAM_ID } from '../../constants';
import { publicKey, rustEnum, uint64 } from '../../utils/layout';
import { Percentage } from './percentage';

type FeeStructure = {
  traderFee: Percentage;
  ownerFee: Percentage;
};

// https://github.com/aldrin-exchange/aldrin-sdk/blob/78527e3adfc02e186956f420b5083fa0950e7263/src/pools/layout.ts
const FEES_LAYOUT = struct(
  [
    uint64('tradeFeeNumerator', true),
    uint64('tradeFeeDenominator', true),
    uint64('ownerTradeFeeNumerator', true),
    uint64('ownerTradeFeeDenominator', true),
    uint64('ownerWithdrawFeeNumerator', true),
    uint64('ownerWithdrawFeeDenominator', true),
  ],
  'fees'
);

const POOL_FIELDS_COMMON = [
  blob(8, 'padding'),
  publicKey('lpTokenFreezeVault'),
  publicKey('poolMint'),
  publicKey('baseTokenVault'),
  publicKey('baseTokenMint'),
  publicKey('quoteTokenVault'),
  publicKey('quoteTokenMint'),
  publicKey('poolSigner'),
  u8('poolSignerNonce'),
  publicKey('authority'),
  publicKey('initializerAccount'),
  publicKey('feeBaseAccount'),
  publicKey('feeQuoteAccount'),
  publicKey('feePoolTokenAccount'),
  FEES_LAYOUT,
];

export const POOL_LAYOUT = struct([
  blob(8, 'padding'),
  publicKey('lpTokenFreezeVault'),
  publicKey('poolMint'),
  publicKey('baseTokenVault'),
  publicKey('baseTokenMint'),
  publicKey('quoteTokenVault'),
  publicKey('quoteTokenMint'),
  publicKey('poolSigner'),
  u8('poolSignerNonce'),
  publicKey('authority'),
  publicKey('initializerAccount'),
  publicKey('feeBaseAccount'),
  publicKey('feeQuoteAccount'),
  publicKey('feePoolTokenAccount'),
  struct(
    [
      uint64('tradeFeeNumerator', true),
      uint64('tradeFeeDenominator', true),
      uint64('ownerTradeFeeNumerator', true),
      uint64('ownerTradeFeeDenominator', true),
      uint64('ownerWithdrawFeeNumerator', true),
      uint64('ownerWithdrawFeeDenominator', true),
    ],
    'fees'
  ),
]);

export const POOL_V2_LAYOUT = struct([
  ...POOL_FIELDS_COMMON,
  u8('curveType'),
  publicKey('curve'),
]);

export const STABLE_CURVE_LAYOUT = struct([blob(8, 'padding'), uint64('amp')]);

export const SWAP_INSTRUCTION_LAYOUT = struct([
  blob(8, 'instruction'),
  uint64('tokens'),
  uint64('minTokens'),
  rustEnum([new Structure([], 'bid'), new Structure([], 'ask')], 'side'),
]);

export function accountInfoToAldrinPoolState(
  address: PublicKey,
  accountInfo: AccountInfo<Buffer>
): AldrinPoolState {
  const isV2 = accountInfo.owner.equals(ALDRIN_SWAP_V2_PROGRAM_ID)
    ? true
    : false;
  const decoded = (isV2 ? POOL_V2_LAYOUT : POOL_LAYOUT).decode(
    accountInfo.data
  );

  return {
    isV2,
    address,
    poolMint: decoded.poolMint,
    baseTokenVault: decoded.baseTokenVault,
    baseTokenMint: decoded.baseTokenMint,
    quoteTokenVault: decoded.quoteTokenVault,
    quoteTokenMint: decoded.quoteTokenMint,
    poolSigner: decoded.poolSigner,
    feeBaseAccount: decoded.feeBaseAccount,
    feeQuoteAccount: decoded.feeQuoteAccount,
    feePoolTokenAccount: decoded.feePoolTokenAccount,
    fees: {
      traderFee: Percentage.fromFraction(
        decoded.fees.tradeFeeNumerator,
        decoded.fees.tradeFeeDenominator
      ),
      ownerFee: Percentage.fromFraction(
        decoded.fees.ownerTradeFeeNumerator,
        decoded.fees.ownerTradeFeeDenominator
      ),
    },
    curveType: decoded.curveType,
    curve: decoded.curve,
  };
}

export interface AldrinPoolState {
  isV2: Boolean;
  address: PublicKey;
  poolMint: PublicKey;
  baseTokenVault: PublicKey;
  baseTokenMint: PublicKey;
  quoteTokenVault: PublicKey;
  quoteTokenMint: PublicKey;
  poolSigner: PublicKey;
  feeBaseAccount: PublicKey;
  feeQuoteAccount: PublicKey;
  feePoolTokenAccount: PublicKey;
  fees: FeeStructure;
  curveType?: number;
  curve?: PublicKey;
}
