import JSBI from 'jsbi';
import { SwapResult, toDecimal, ZERO, ceilingDivision } from '../utils';
import Decimal from 'decimal.js';
import { Fraction } from '..';

export class TokenSwapConstantProduct {
  constructor(
    private tokenAmounts: JSBI[],
    private traderFee: Fraction,
    private ownerFee: Fraction,
    private feesOnInput: boolean = true
  ) {}

  public exchange(
    inputTradeAmount: JSBI,
    slippageTolerance: Fraction,
    outputIndex: number
  ): SwapResult {
    let inputIndex = outputIndex === 0 ? 1 : 0;
    const newInputTradeAmount = this.feesOnInput
      ? this.getAmountLessFees(inputTradeAmount)
      : inputTradeAmount;

    let expectedOutputAmount = this.getExpectedOutputAmount(
      newInputTradeAmount,
      inputIndex,
      outputIndex
    );

    let fees = this.getFees(
      this.feesOnInput ? inputTradeAmount : expectedOutputAmount
    );

    if (!this.feesOnInput) {
      expectedOutputAmount = this.getAmountLessFees(expectedOutputAmount);
    }

    return {
      priceImpact: this.getPriceImpact(
        newInputTradeAmount,
        expectedOutputAmount,
        inputIndex,
        outputIndex
      ),
      fees,
      expectedOutputAmount,
      minOutputAmount: this.getMinimumOutputAmount(
        expectedOutputAmount,
        slippageTolerance
      ),
    };
  }

  private getPriceImpact(
    inputTradeAmountJSBI: JSBI,
    expectedOutputAmountJSBI: JSBI,
    inputIndex: number,
    outputIndex: number
  ): Decimal {
    if (
      JSBI.equal(inputTradeAmountJSBI, ZERO) ||
      JSBI.equal(this.tokenAmounts[inputIndex], ZERO) ||
      JSBI.equal(this.tokenAmounts[outputIndex], ZERO)
    ) {
      return new Decimal(0);
    }

    const noSlippageOutputAmount = toDecimal(
      this.getExpectedOutputAmountWithNoSlippage(
        inputTradeAmountJSBI,
        inputIndex,
        outputIndex
      )
    );
    const expectedOutputAmount = toDecimal(expectedOutputAmountJSBI);
    const impact = noSlippageOutputAmount
      .sub(expectedOutputAmount)
      .div(noSlippageOutputAmount)
      .abs();

    return impact;
  }

  private getFees(inputTradeAmount: JSBI): JSBI {
    const tradingFee = JSBI.equal(this.traderFee.numerator, ZERO)
      ? ZERO
      : JSBI.divide(
          JSBI.multiply(inputTradeAmount, this.traderFee.numerator),
          this.traderFee.denominator
        );

    const ownerFee = JSBI.equal(this.ownerFee.numerator, ZERO)
      ? ZERO
      : JSBI.divide(
          JSBI.multiply(inputTradeAmount, this.ownerFee.numerator),
          this.ownerFee.denominator
        );

    return JSBI.add(tradingFee, ownerFee);
  }

  private getExpectedOutputAmount(
    inputTradeAmount: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    return this.getOutputAmount(inputTradeAmount, inputIndex, outputIndex);
  }

  private getExpectedOutputAmountWithNoSlippage(
    inputTradeAmount: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    if (JSBI.equal(this.tokenAmounts[inputIndex], ZERO)) {
      return this.tokenAmounts[outputIndex];
    }

    let expectedOutputAmountWithNoSlippage = JSBI.divide(
      JSBI.multiply(inputTradeAmount, this.tokenAmounts[outputIndex]),
      this.tokenAmounts[inputIndex]
    );

    if (this.feesOnInput) {
      return expectedOutputAmountWithNoSlippage;
    } else {
      return this.getAmountLessFees(expectedOutputAmountWithNoSlippage);
    }
  }

  private getMinimumOutputAmount(
    expectedOutputAmountJSBI: JSBI,
    slippageTolerance: Fraction
  ): JSBI {
    return JSBI.divide(
      JSBI.multiply(
        expectedOutputAmountJSBI,
        JSBI.subtract(
          slippageTolerance.denominator,
          slippageTolerance.numerator
        )
      ),
      slippageTolerance.denominator
    );
  }

  private getAmountLessFees(tradeAmount: JSBI): JSBI {
    return JSBI.subtract(tradeAmount, this.getFees(tradeAmount));
  }

  private getOutputAmount(
    inputTradeAmount: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    const [poolInputAmount, poolOutputAmount] = [
      this.tokenAmounts[inputIndex],
      this.tokenAmounts[outputIndex],
    ];

    const invariant = this.getInvariant();

    const [newPoolOutputAmount] = ceilingDivision(
      invariant,
      JSBI.add(poolInputAmount, inputTradeAmount)
    );

    return JSBI.subtract(poolOutputAmount, newPoolOutputAmount);
  }

  getInvariant() {
    return JSBI.multiply(this.tokenAmounts[0], this.tokenAmounts[1]);
  }
}
