import { Fraction, Stable } from '@jup-ag/math';
import {
  FEE_DENOMINATOR,
  StableSwapNPool,
} from '@mercurial-finance/stable-swap-n-pool';
import { PublicKey, TransactionInstruction } from '@solana/web3.js';
import JSBI from 'jsbi';
import {
  AccountInfoMap,
  Amm,
  mapAddressToAccountInfos,
  Quote,
  QuoteParams,
  SwapParams,
} from '../amm';
import { createMercurialExchangeInstruction } from '../jupiterInstruction';
import { deserializeAccount } from '../../utils/deserializeAccount';
import { AccountInfo } from '@solana/spl-token';
import { ZERO_FRACTION } from '../../constants';

export class MercurialAmm implements Amm {
  label = 'Mercurial';
  private tokenAccounts: AccountInfo[] = [];
  private calculator: Stable | undefined;

  constructor(private stableSwapNPool: StableSwapNPool) {}

  get id() {
    return this.stableSwapNPool.poolAccount.toBase58();
  }

  getAccountsForUpdate(): PublicKey[] {
    return this.stableSwapNPool.tokenAccounts;
  }

  update(accountInfoMap: AccountInfoMap) {
    let tokenAccountInfos = mapAddressToAccountInfos(
      accountInfoMap,
      this.getAccountsForUpdate()
    );

    this.tokenAccounts = tokenAccountInfos
      .map((info) => deserializeAccount(info.data))
      .filter((x): x is AccountInfo => x !== null);

    this.calculator = new Stable(
      JSBI.BigInt(this.stableSwapNPool.amplificationCoefficient),
      this.tokenAccounts.map((tokenAccount) => {
        return JSBI.BigInt(tokenAccount.amount.toString());
      }),
      this.stableSwapNPool.precisionMultipliers.map((precisionMultiplier) =>
        JSBI.BigInt(precisionMultiplier)
      ),
      new Fraction(
        JSBI.BigInt(this.stableSwapNPool.feeNumerator),
        JSBI.BigInt(FEE_DENOMINATOR)
      )
    );
  }

  getQuote({ sourceMint, destinationMint, amount }: QuoteParams): Quote {
    if (this.tokenAccounts.length === 0) {
      throw new Error('Unable to fetch accounts for specified tokens.');
    }

    if (this.calculator === undefined) {
      throw new Error('Unable to fetch accounts for Curve calculator.');
    }

    const inputIndex = this.tokenAccounts.findIndex((tokenAccount) =>
      tokenAccount.mint.equals(sourceMint)
    );
    const outputIndex = this.tokenAccounts.findIndex((tokenAccount) =>
      tokenAccount.mint.equals(destinationMint)
    );
    const result = this.calculator.exchange(
      JSBI.BigInt(amount),
      ZERO_FRACTION,
      inputIndex,
      outputIndex
    );

    const feePct = this.stableSwapNPool.feeNumerator / FEE_DENOMINATOR;

    return {
      notEnoughLiquidity: false,
      inAmount: amount,
      outAmount: JSBI.toNumber(result.expectedOutputAmount),
      feeAmount: JSBI.toNumber(result.fees),
      feeMint: destinationMint.toBase58(),
      feePct: feePct,
      priceImpactPct: result.priceImpact.toNumber(),
    };
  }

  createSwapInstructions({
    sourceTokenAccount,
    destinationTokenAccount,
    userTransferAuthority,
    amount,
    minimumOutAmount,
    platformFee,
  }: SwapParams) {
    return [createMercurialExchangeInstruction(
      this.stableSwapNPool,
      sourceTokenAccount,
      destinationTokenAccount,
      userTransferAuthority,
      amount,
      minimumOutAmount,
      platformFee
    )];
  }

  get reserveTokenMints() {
    return this.stableSwapNPool.tokenMints;
  }
}
