import JSBI from 'jsbi';
import { Curve } from '../math/curve';
import { ONE, SwapResult, toDecimal, TWO, ZERO } from '../utils';
import Decimal from 'decimal.js';
import { Fraction } from '..';

export class TokenSwapStable {
  private curve: Curve;

  constructor(
    amp: JSBI,
    private tokenAmounts: JSBI[],
    private traderFee: Fraction,
    private ownerFee: Fraction
  ) {
    this.curve = new Curve(TWO, amp, this.tokenAmounts, [ONE, ONE]);
  }

  public exchange(
    inputTradeAmount: JSBI,
    slippageTolerance: Fraction,
    outputIndex: number
  ): SwapResult {
    let inputIndex = outputIndex === 0 ? 1 : 0;
    let expectedOutputAmount = this.getExpectedOutputAmount(
      inputTradeAmount,
      inputIndex,
      outputIndex
    );

    return {
      priceImpact: this.getPriceImpact(
        inputTradeAmount,
        expectedOutputAmount,
        inputIndex,
        outputIndex
      ),
      fees: this.getFees(inputTradeAmount),
      expectedOutputAmount,
      minOutputAmount: this.getMinimumOutputAmount(
        expectedOutputAmount,
        slippageTolerance
      ),
    };
  }

  private getPriceImpact(
    inputTradeAmountJSBI: JSBI,
    expectedOutputAmountJSBI: JSBI,
    inputIndex: number,
    outputIndex: number
  ): Decimal {
    if (
      JSBI.equal(inputTradeAmountJSBI, ZERO) ||
      JSBI.equal(this.tokenAmounts[inputIndex], ZERO) ||
      JSBI.equal(this.tokenAmounts[outputIndex], ZERO)
    ) {
      return new Decimal(0);
    }

    const noSlippageOutputAmount = toDecimal(
      this.getExpectedOutputAmountWithNoSlippage(
        inputTradeAmountJSBI,
        inputIndex,
        outputIndex
      )
    );
    const expectedOutputAmount = toDecimal(expectedOutputAmountJSBI);
    const impact = noSlippageOutputAmount
      .sub(expectedOutputAmount)
      .div(noSlippageOutputAmount)
      .abs();

    return impact;
  }

  private getFees(inputTradeAmount: JSBI): JSBI {
    const tradingFee = JSBI.equal(this.traderFee.numerator, ZERO)
      ? ZERO
      : JSBI.divide(
          JSBI.multiply(inputTradeAmount, this.traderFee.numerator),
          this.traderFee.denominator
        );

    const ownerFee = JSBI.equal(this.ownerFee.numerator, ZERO)
      ? ZERO
      : JSBI.divide(
          JSBI.multiply(inputTradeAmount, this.ownerFee.numerator),
          this.ownerFee.denominator
        );

    return JSBI.add(tradingFee, ownerFee);
  }

  private getExpectedOutputAmount(
    inputTradeAmount: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    const inputTradeAmountLessFees =
      this.getInputAmountLessFees(inputTradeAmount);

    return this.getOutputAmount(
      inputTradeAmountLessFees,
      inputIndex,
      outputIndex
    );
  }

  private getExpectedOutputAmountWithNoSlippage(
    inputTradeAmount: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    const inputTradeAmountLessFees =
      this.getInputAmountLessFees(inputTradeAmount);

    return this.getOutputAmountWithNoSlippage(
      inputTradeAmountLessFees,
      inputIndex,
      outputIndex
    );
  }

  private getMinimumOutputAmount(
    expectedOutputAmountJSBI: JSBI,
    slippageTolerance: Fraction
  ): JSBI {
    return JSBI.divide(
      JSBI.multiply(
        expectedOutputAmountJSBI,
        JSBI.subtract(
          slippageTolerance.denominator,
          slippageTolerance.numerator
        )
      ),
      slippageTolerance.denominator
    );
  }

  private getInputAmountLessFees(inputTradeAmount: JSBI): JSBI {
    return JSBI.subtract(inputTradeAmount, this.getFees(inputTradeAmount));
  }

  private getOutputAmount(
    inputTradeAmountLessFees: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    return this.curve.exchange(
      inputIndex,
      outputIndex,
      inputTradeAmountLessFees,
      false
    );
  }

  private getOutputAmountWithNoSlippage(
    inputTradeAmountLessFees: JSBI,
    inputIndex: number,
    outputIndex: number
  ): JSBI {
    return this.curve.computeBaseY(
      inputIndex,
      outputIndex,
      inputTradeAmountLessFees
    );
  }
}
