function prepareRemainingAccounts(
  amount: number | null,
  feeAccount: PublicKey | undefined
): AccountMeta[] {
  const remainingAccounts = [];

  if (amount === null) {
    remainingAccounts.push({
      pubkey: TOKEN_LEDGER,
      isSigner: false,
      isWritable: true,
    });
  }
  if (feeAccount) {
    remainingAccounts.push({
      pubkey: feeAccount,
      isSigner: false,
      isWritable: true,
    });
  }

  return remainingAccounts;
}
