import { findProgramAddressSync } from '@project-serum/anchor/dist/cjs/utils/pubkey';
import { u64 } from '@solana/spl-token';
import {
  AccountInfo,
  PublicKey,
  TransactionInstruction,
} from '@solana/web3.js';
import Decimal from 'decimal.js';
import {
  AccountInfoMap,
  Amm,
  mapAddressToAccountInfos,
  Quote,
  QuoteParams,
  SwapParams,
} from '../amm';
import { createTokenSwapInstruction } from '../jupiterInstruction';
import { StepTokenSwapLayout, TokenSwapLayout } from './tokenSwapLayout';
import { deserializeAccount } from '../../utils/deserializeAccount';
import {
  Fraction,
  TokenSwapConstantProduct,
  TokenSwapStable,
} from '@jup-ag/math';
import JSBI from 'jsbi';
import { AccountInfo as TokenAccountInfo } from '@solana/spl-token';
import { ZERO_FRACTION } from '../../constants';
import { STEP_TOKEN_SWAP_PROGRAM_ID } from './splTokenSwapPools';

enum CurveType {
  ConstantProduct = 0,
  Stable = 2,
}

// Abstract any SPL token swap based AMM
export class SplTokenSwapAmm implements Amm {
  private tokenSwapState: TokenSwapState;
  private curveType: CurveType;

  private tokenAccounts: TokenAccountInfo[] = [];
  calculator: TokenSwapConstantProduct | TokenSwapStable | undefined;

  constructor(
    address: PublicKey,
    swapStateAccountInfo: AccountInfo<Buffer>,
    public label: string
  ) {
    this.tokenSwapState = accountInfoToTokenSwapState(
      address,
      swapStateAccountInfo
    );

    this.curveType = this.tokenSwapState.curveType;
    if (!(this.curveType in CurveType)) {
      throw new Error(
        `curveType ${this.tokenSwapState.curveType} is not supported`
      );
    }
  }

  get id() {
    return this.tokenSwapState.address.toBase58();
  }

  getAccountsForUpdate(): PublicKey[] {
    return [
      this.tokenSwapState.tokenAccountA,
      this.tokenSwapState.tokenAccountB,
    ];
  }

  update(accountInfoMap: AccountInfoMap): void {
    const tokenAccountInfos = mapAddressToAccountInfos(
      accountInfoMap,
      this.getAccountsForUpdate()
    );

    this.tokenAccounts = tokenAccountInfos.map((info) => {
      const tokenAccount = deserializeAccount(info.data);
      if (!tokenAccount) {
        throw new Error('Invalid token account');
      }
      return tokenAccount;
    });

    if (this.tokenSwapState.curveType === CurveType.ConstantProduct) {
      this.calculator = new TokenSwapConstantProduct(
        this.tokenAccounts.map((tokenAccount) => {
          return JSBI.BigInt(tokenAccount.amount.toString());
        }),
        new Fraction(
          JSBI.BigInt(this.tokenSwapState.tradeFeeNumerator.toString()),
          JSBI.BigInt(this.tokenSwapState.tradeFeeDenominator.toString())
        ),
        new Fraction(
          JSBI.BigInt(this.tokenSwapState.ownerTradeFeeNumerator.toString()),
          JSBI.BigInt(this.tokenSwapState.ownerTradeFeeDenominator.toString())
        )
      );
    } else if (this.tokenSwapState.curveType === CurveType.Stable) {
      this.calculator = new TokenSwapStable(
        JSBI.BigInt(this.tokenSwapState.curveParameters[0]),
        this.tokenAccounts.map((tokenAccount) => {
          return JSBI.BigInt(tokenAccount.amount.toString());
        }),
        new Fraction(
          JSBI.BigInt(this.tokenSwapState.tradeFeeNumerator.toString()),
          JSBI.BigInt(this.tokenSwapState.tradeFeeDenominator.toString())
        ),
        new Fraction(
          JSBI.BigInt(this.tokenSwapState.ownerTradeFeeNumerator.toString()),
          JSBI.BigInt(this.tokenSwapState.ownerTradeFeeDenominator.toString())
        )
      );
    }
  }

  getQuote({ sourceMint, amount }: QuoteParams): Quote {
    if (this.tokenAccounts.length === 0) {
      throw new Error('Unable to fetch accounts for specified tokens.');
    }

    if (this.calculator === undefined) {
      throw new Error('Unable to fetch accounts for Curve calculator.');
    }

    let feePct = new Decimal(this.tokenSwapState.tradeFeeNumerator.toString())
      .div(this.tokenSwapState.tradeFeeDenominator.toString())
      .add(
        new Decimal(this.tokenSwapState.ownerTradeFeeNumerator.toString()).div(
          this.tokenSwapState.ownerTradeFeeDenominator.toString()
        )
      );

    const outputIndex = this.tokenAccounts[0].mint.equals(sourceMint) ? 1 : 0;
    let result = this.calculator.exchange(
      JSBI.BigInt(amount),
      ZERO_FRACTION,
      outputIndex
    );

    return {
      notEnoughLiquidity: false,
      inAmount: amount,
      outAmount: JSBI.toNumber(result.expectedOutputAmount),
      feeAmount: JSBI.toNumber(result.fees),
      feeMint: sourceMint.toBase58(),
      feePct: feePct.toNumber(),
      priceImpactPct: result.priceImpact.toNumber(),
    };
  }

  createSwapInstructions({
    sourceMint,
    sourceTokenAccount,
    destinationTokenAccount,
    userTransferAuthority,
    amount,
    minimumOutAmount,
    platformFee,
  }: SwapParams) {
    return [createTokenSwapInstruction(
      this.tokenSwapState,
      sourceMint,
      sourceTokenAccount,
      destinationTokenAccount,
      userTransferAuthority,
      amount,
      minimumOutAmount,
      platformFee,
      this.tokenSwapState.programId.equals(STEP_TOKEN_SWAP_PROGRAM_ID)
    )];
  }

  get reserveTokenMints() {
    return [this.tokenSwapState.mintA, this.tokenSwapState.mintB];
  }
}

function accountInfoToTokenSwapState(
  address: PublicKey,
  tokenSwapAccountInfo: AccountInfo<Buffer>
): TokenSwapState {
  const programId = tokenSwapAccountInfo.owner;

  // The layout difference only affects fields we do not actively use
  const tokenSwapData = programId.equals(STEP_TOKEN_SWAP_PROGRAM_ID)
    ? StepTokenSwapLayout.decode(tokenSwapAccountInfo.data)
    : TokenSwapLayout.decode(tokenSwapAccountInfo.data);

  if (!tokenSwapData.isInitialized) {
    throw new Error(`Invalid token swap state`);
  }

  const [authority] = findProgramAddressSync([address.toBuffer()], programId);

  const poolToken = new PublicKey(tokenSwapData.tokenPool);
  const feeAccount = new PublicKey(tokenSwapData.feeAccount);
  const tokenAccountA = new PublicKey(tokenSwapData.tokenAccountA);
  const tokenAccountB = new PublicKey(tokenSwapData.tokenAccountB);
  const mintA = new PublicKey(tokenSwapData.mintA);
  const mintB = new PublicKey(tokenSwapData.mintB);
  const tokenProgramId = new PublicKey(tokenSwapData.tokenProgramId);

  const tradeFeeNumerator = u64.fromBuffer(tokenSwapData.tradeFeeNumerator);
  const tradeFeeDenominator = u64.fromBuffer(tokenSwapData.tradeFeeDenominator);
  const ownerTradeFeeNumerator = u64.fromBuffer(
    tokenSwapData.ownerTradeFeeNumerator
  );
  const ownerTradeFeeDenominator = u64.fromBuffer(
    tokenSwapData.ownerTradeFeeDenominator
  );
  const ownerWithdrawFeeNumerator = u64.fromBuffer(
    tokenSwapData.ownerWithdrawFeeNumerator
  );
  const ownerWithdrawFeeDenominator = u64.fromBuffer(
    tokenSwapData.ownerWithdrawFeeDenominator
  );

  const curveType = tokenSwapData.curveType;
  const curveParameters = tokenSwapData.curveParameters;
  const poolNonce = tokenSwapData.poolNonce;

  return {
    address,
    programId,
    tokenProgramId,
    poolToken,
    feeAccount,
    authority,
    tokenAccountA,
    tokenAccountB,
    mintA,
    mintB,
    tradeFeeNumerator,
    tradeFeeDenominator,
    ownerTradeFeeNumerator,
    ownerTradeFeeDenominator,
    ownerWithdrawFeeNumerator,
    ownerWithdrawFeeDenominator,
    curveType,
    curveParameters,
    poolNonce,
  };
}

export interface TokenSwapState {
  address: PublicKey;
  programId: PublicKey;
  tokenProgramId: PublicKey;
  poolToken: PublicKey;
  feeAccount: PublicKey;
  authority: PublicKey;
  tokenAccountA: PublicKey;
  tokenAccountB: PublicKey;
  mintA: PublicKey;
  mintB: PublicKey;
  tradeFeeNumerator: u64;
  tradeFeeDenominator: u64;
  ownerTradeFeeNumerator: u64;
  ownerTradeFeeDenominator: u64;
  ownerWithdrawFeeNumerator: u64;
  ownerWithdrawFeeDenominator: u64;
  curveType: number;
  curveParameters: number[];
  poolNonce: number;
}
