// https://github.com/raydium-io/raydium-ui/blob/4048286f79fc4b71c3ffbfd9095470ab0c7d3862/src/utils/liquidity.ts#L30-L82

import { createProgramAddressSync } from '@project-serum/anchor/dist/cjs/utils/pubkey';
import { Market, OpenOrders } from '@project-serum/serum';
import { u64 } from '@solana/spl-token';
import {
  AccountInfo,
  PublicKey,
  TransactionInstruction,
} from '@solana/web3.js';
import BN from 'bn.js';
import Decimal from 'decimal.js';
import {
  AccountInfoMap,
  Amm,
  mapAddressToAccountInfos,
  Quote,
  QuoteParams,
  SwapParams,
} from '../amm';
import { createRaydiumSwapInstruction } from '../jupiterInstruction';
import { AMM_INFO_LAYOUT_V4 } from './pools';

// Explained here
// https://discordapp.com/channels/813741812598439958/813750197423308820/900288485028683776
// total_pc = amminfo.pc_vault.balance + amminfo.openorder.total_quote - amminfo.need_taken_pnl_pc
// total_coin = amminfo.coin_vault.balance + amminfo.openorder.total_base - amminfo.need_taken_pnl_coin

type SerumMarketKeys = {
  serumBids: PublicKey;
  serumAsks: PublicKey;
  serumEventQueue: PublicKey;
  serumCoinVaultAccount: PublicKey;
  serumPcVaultAccount: PublicKey;
  serumVaultSigner: PublicKey;
};

export class RaydiumAmm implements Amm {
  label = 'Raydium';

  coinMint: PublicKey;
  pcMint: PublicKey;

  status: number;
  serumProgramId: PublicKey;
  serumMarket: PublicKey;
  ammOpenOrders: PublicKey;
  ammTargetOrders: PublicKey;
  poolCoinTokenAccount: PublicKey;
  poolPcTokenAccount: PublicKey;

  serumMarketKeys: SerumMarketKeys | undefined;

  coinReserve: u64 | undefined;
  pcReserve: u64 | undefined;

  swapFeeNumerator: u64;
  swapFeeDenominator: u64;

  constructor(
    public ammId: PublicKey,
    public ammAccountInfo: AccountInfo<Buffer>
  ) {
    const decoded = AMM_INFO_LAYOUT_V4.decode(ammAccountInfo.data);

    this.status = decoded.status;
    this.coinMint = new PublicKey(decoded.coinMintAddress);
    this.pcMint = new PublicKey(decoded.pcMintAddress);

    this.poolCoinTokenAccount = new PublicKey(decoded.poolCoinTokenAccount);
    this.poolPcTokenAccount = new PublicKey(decoded.poolPcTokenAccount);

    this.swapFeeNumerator = new u64(decoded.swapFeeNumerator);
    this.swapFeeDenominator = new u64(decoded.swapFeeDenominator);

    this.serumProgramId = new PublicKey(decoded.serumProgramId);
    this.serumMarket = new PublicKey(decoded.serumMarket);
    this.ammOpenOrders = new PublicKey(decoded.ammOpenOrders);
    this.ammTargetOrders = new PublicKey(decoded.ammTargetOrders);
  }

  get id() {
    return this.ammId.toBase58();
  }

  getAccountsForUpdate(): PublicKey[] {
    return [
      this.ammId,
      this.poolCoinTokenAccount,
      this.poolPcTokenAccount,
      this.ammOpenOrders,
      this.serumMarket,
    ];
  }

  update(accountInfoMap: AccountInfoMap) {
    const [
      ammAccountInfo,
      poolCoinTokenAccountInfo,
      poolPcTokenAccountInfo,
      ammOpenOrdersAccountInfo,
      serumMarketMarketInfo,
    ] = mapAddressToAccountInfos(accountInfoMap, this.getAccountsForUpdate());

    this.ammAccountInfo = ammAccountInfo!;

    const [coinAmount, pcAmount] = [
      RaydiumAmm.tokenAmountAccessor(poolCoinTokenAccountInfo!),
      RaydiumAmm.tokenAmountAccessor(poolPcTokenAccountInfo!),
    ];

    const openOrders = OpenOrders.fromAccountInfo(
      this.ammOpenOrders,
      ammOpenOrdersAccountInfo!,
      ammOpenOrdersAccountInfo!.owner
    );

    // TODO: Possibly better design is to have this passed to the ctor, so it isn't an independant instruction
    const decodedMarket = Market.getLayout(this.serumProgramId).decode(
      serumMarketMarketInfo!.data
    );
    const serumVaultSigner = createProgramAddressSync(
      [
        this.serumMarket.toBuffer(),
        decodedMarket.vaultSignerNonce.toArrayLike(Buffer, 'le', 8),
      ],
      this.serumProgramId
    );
    this.serumMarketKeys = {
      serumBids: decodedMarket.bids,
      serumAsks: decodedMarket.asks,
      serumEventQueue: decodedMarket.eventQueue,
      serumCoinVaultAccount: decodedMarket.baseVault,
      serumPcVaultAccount: decodedMarket.quoteVault,
      serumVaultSigner: serumVaultSigner,
    };

    const decoded = AMM_INFO_LAYOUT_V4.decode(this.ammAccountInfo.data);

    this.coinReserve = coinAmount
      .add(openOrders.baseTokenTotal)
      .sub(new u64(String(decoded.needTakePnlCoin)));

    this.pcReserve = pcAmount
      .add(openOrders.quoteTokenTotal)
      .sub(new u64(String(decoded.needTakePnlPc)));
  }

  private static tokenAmountAccessor(
    tokenAccountInfo: AccountInfo<Buffer>
  ): u64 {
    return u64.fromBuffer(tokenAccountInfo.data.slice(64, 64 + 8));
  }

  getQuote({ sourceMint, amount }: QuoteParams): Quote {
    if (!this.coinReserve || !this.pcReserve) {
      throw new Error('Pool token accounts balances not refreshed or empty');
    }

    const { coinReserve, pcReserve } = this;

    const [inputReserve, outputReserve] = this.coinMint.equals(sourceMint)
      ? [coinReserve, pcReserve]
      : [pcReserve, coinReserve];

    const inAmountBN = new BN(String(amount));
    const feeAmount = inAmountBN
      .mul(this.swapFeeNumerator)
      .div(this.swapFeeDenominator);
    const inAmountAfterFee = inAmountBN.sub(feeAmount);

    const numerator = inAmountAfterFee.mul(outputReserve);
    const denominator = inputReserve.add(inAmountAfterFee);

    const outAmountWithoutPriceImpact = inAmountAfterFee
      .mul(outputReserve)
      .div(inputReserve);

    const outAmountBN = numerator.div(denominator);
    const outAmount = Number(outAmountBN.toString());

    return {
      notEnoughLiquidity: false,
      inAmount: amount,
      outAmount,
      feeAmount: Number(feeAmount.toString()),
      feeMint: sourceMint.toBase58(),
      feePct: Number(
        new Decimal(this.swapFeeNumerator.toString())
          .div(this.swapFeeDenominator.toString())
          .toString()
      ),
      priceImpactPct: Number(
        new Decimal(outAmountWithoutPriceImpact.sub(outAmountBN).toString())
          .div(outAmountWithoutPriceImpact.toString())
          .toString()
      ),
    };
  }

  createSwapInstructions({
    sourceTokenAccount,
    destinationTokenAccount,
    userTransferAuthority,
    amount,
    minimumOutAmount,
    platformFee,
  }: SwapParams) {
    return [
      createRaydiumSwapInstruction(
        this,
        sourceTokenAccount,
        destinationTokenAccount,
        userTransferAuthority,
        amount,
        minimumOutAmount,
        platformFee
      ),
    ];
  }

  get reserveTokenMints() {
    return [this.coinMint, this.pcMint];
  }
}
