import { struct, u8, Layout, blob } from '@solana/buffer-layout';

const publicKey = (property: string = 'publicKey'): Layout => {
  return blob(32, property);
};

/**
 * Layout for a 64bit unsigned value
 */
const uint64 = (property: string = 'uint64'): Layout => {
  return blob(8, property);
};

export const TokenSwapLayout = struct([
  u8('version'),
  u8('isInitialized'),
  u8('bumpSeed'),
  publicKey('tokenProgramId'),
  publicKey('tokenAccountA'),
  publicKey('tokenAccountB'),
  publicKey('tokenPool'),
  publicKey('mintA'),
  publicKey('mintB'),
  publicKey('feeAccount'),
  uint64('tradeFeeNumerator'),
  uint64('tradeFeeDenominator'),
  uint64('ownerTradeFeeNumerator'),
  uint64('ownerTradeFeeDenominator'),
  uint64('ownerWithdrawFeeNumerator'),
  uint64('ownerWithdrawFeeDenominator'),
  uint64('hostFeeNumerator'),
  uint64('hostFeeDenominator'),
  u8('curveType'),
  blob(32, 'curveParameters'),
]);

export const StepTokenSwapLayout = struct([
  u8('version'),
  u8('isInitialized'),
  u8('bumpSeed'),
  publicKey('tokenProgramId'),
  publicKey('tokenAccountA'),
  publicKey('tokenAccountB'),
  publicKey('tokenPool'),
  publicKey('mintA'),
  publicKey('mintB'),
  publicKey('feeAccount'),
  uint64('tradeFeeNumerator'),
  uint64('tradeFeeDenominator'),
  uint64('ownerTradeFeeNumerator'),
  uint64('ownerTradeFeeDenominator'),
  uint64('ownerWithdrawFeeNumerator'),
  uint64('ownerWithdrawFeeDenominator'),
  u8('curveType'),
  blob(32, 'curveParameters'),
  u8('poolNonce'),
]);
